body {
  background-color: rgb(182, 173, 173);
}

/* Style buttons */
.note-btn {
  background-color: rgba(30, 143, 255, 0); /* Blue background */
  border: none; /* Remove borders */
  color: rgb(53, 52, 52); /* White text */
  margin: 3px;
  font-size: 25px; /* Set a font size */
  cursor: pointer; /* Mouse pointer on hover */
  border-radius: 5%;
}

header form {
  display: flex;
  flex-direction: column;
  width: 40rem;
}

form > * {
  margin-bottom: 5px;
  outline: none;
  font-size: 1.3rem;
  padding: 7px;
  border: 1px solid #777;
  border-radius: 3%;
}

form textarea {
  padding: 7px 0 40px 5px;
  font-family: inherit;
}

::placeholder {
  font-size: 1.3rem;
}

form button {
  background-color: orange;
  font-size: 1.6rem;
  cursor: pointer;
  transition: all 0.5s;
}

form button:hover {
  background-color: orangered;
}

.notes {
  padding: 1rem;
  background-color: white;
  border-radius: 5px;
  margin: 1rem 0;
}

.note-counter,
.note-text {
  font-size: 1.4rem;
}

.note-title {
  font-size: 1.6rem;
}

.phone {
  font-size: 18px;
  background-color: aqua;
  color: #000;
  border-collapse: collapse;
}
.phone td {
  padding: 10px;
  border: 1px solid #fff;
}
.phone td[colspan="2"] {
  font-weight: bold;
  text-align: center;
  background-color: aqua;
}
